.footer__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    font-family: 'Poppins';
}

.footer__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 100%;
    height: 300px;
    margin-top: 150px;
}

.footer__content__item {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 15%;
}

.footer__content__item__description {
    padding: 5%;
    margin: 0;
}

p {
    margin: 0;
}

.footer__icon {
    padding: 0 10px;
    margin: 0;
    font-size: 1.8rem;
}

.footer__icon:hover {
    color: #f5f5f5;
}

/* media */
@media screen and (max-width: 1110px) {
    .footer__content__item {
        margin-top: 0px;
    }
}
@media screen and (max-width: 768px) {
    .footer__content {
        display: grid;
        grid-template-columns: 1fr;
        width: 100%;
        height: 400px;
        margin-top: 150px;
    }
}